@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
:root{
	--purple: #5c5e99;
	--pink: #db8588;
}

*{
	font-family: Lobster;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

#credits{
	position: fixed;
	bottom: 1vh;
	right: 1vw;
}

#app {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 3vh 3vw;
}

button:focus{
	outline: none;
}

button{
	cursor: pointer;
}

#loading-image{
	cursor: pointer;
	background-image: url(/wheres-waldo/static/media/title.00385daf.jpg);
	background-size: contain;
	background-repeat: no-repeat;
	padding: 45vh 45vw;
}

button.back{
	position: absolute;
	height: 4em;
	width: 4em;
	background-image: url(/wheres-waldo/static/media/back.b80b91b6.svg);
	background-size: contain;
	background-repeat: no-repeat;
	background-color: #fff0;
	border: none;
	top: 2em;
	left: 2em;
	opacity: 0.7;
}

button.back:hover{
	height: 4.4em;
	width: 4.4em;
	opacity: 1;
}
#menu{
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;
	width: 100vw;
	height: 40vw;
	overflow: hidden;
}

#nav-buttons{
	position: relative;
	top: -8%;
	left: -1.4%;
}

#menu button{
	border: none;
	padding: 1em 1.5em;
	margin: 0.5em;
	border-radius: 0.2em;
}

#menu button:hover{
	transform: scale(1.1);
}

#menu button.play{
	background-color: #db85888f;
}

#menu button.scores{
	background-color: #5c5e9987;
}

.carousel-container {
	position: relative;
	width: 150%;
	height: 75%;
}

.quick-nav-container{
	position: absolute;
	height: 10%;
	width: 30%;
	top: 75%;
	left: 34%;
	opacity: 0.7;
	z-index: 1;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
}

.quick-nav-button{
	width: 2%;
	padding-top: 2%;
	border-radius: 50%;
	margin: 1.3%;
	background-color: #000;
	z-index: 1;
	cursor: pointer;
}

.quick-nav-button:hover{
	transform: scale(1.2);
}

.quick-nav-button[highlighted=true]{
	background-color: #777;
}

.nav-button {
	position: absolute;
	background: linear-gradient(
		45deg,
		rgba(0, 0, 0, 0),
		rgba(0, 0, 0, 0) 45%,
		rgba(0, 0, 0, 1) 45%,
		rgba(0, 0, 0, 1)
	);
	z-index: 1;
	height: 10%;
	width: 2%;
	top: 40%;
	border-radius: 11%;
	cursor: pointer;
	opacity: 0.6;
}

.nav-button:hover{
	opacity: 0.8;

}

.advance-button {
	left: 66%;
	transform: rotate(45deg);
}

.advance-button:hover {
	transform: rotate(45deg) scale(1.1);
}

.regress-button {
	left: 28%;
	transform: rotate(225deg);
}

.regress-button:hover {
	transform: rotate(225deg) scale(1.1);
}

.carousel-window {
	position: absolute;
	width: 17%;
	height: 85%;
	background-size: contain;
	background-repeat: no-repeat;
	transition: all 0.5s ease-out;
	background-position: center;
	z-index: 0;
}

.carousel-window[carousel-position="dblPrev"] {
	left: 0;
	opacity: 0;
}

.carousel-window[carousel-position="prev"] {
	left: 20%;
	opacity: 0.1;
}

.carousel-window[carousel-position="view"] {
	left: 40%;
}

.carousel-window[carousel-position="next"] {
	left: 60%;
	opacity: 0.1;
}

.carousel-window[carousel-position="dblNext"] {
	left: 80%;
	opacity: 0;
}

#game img {
	cursor: pointer;
	max-width: 90vw;
	max-height: 90vh;
}

#game button {
	position: absolute;
}

#game .menu button {
	border: none;
	background-color: #fffc;
	height: 6em;
	width: 6em;
	border-radius: 50%;
	overflow: hidden;
	opacity: 1;
	box-shadow: 0 0 0.5em white;
}

#game .menu button.hidden {
	height: 0;
	width: 0;
	opacity: 0;
	box-shadow: none;
}

#game .menu button:hover {
	transform: scale(1.1);
	background-color: #fffe;
}

#game .guess-result.correct::before{
	content: "\2713";
	font-size: 3em;
	position: relative;
	top: -0.7em;
	left: -0.65em;
	color: green;
	background-color: #cfcd;
	padding: 0 0.2em;
	border-radius: 50%;
	box-shadow: 0 0 0.5em #cfc;
}

#game .guess-result.incorrect::before{
	box-sizing: border-box;
	content: "\2715";
	font-size: 3em;
	position: relative;
	top: -0.7em;
	left: -0.65em;
	color: red;
	background-color: #fccd;
	padding: 0 0.2em;
	border-radius: 50%;
	box-shadow: 0 0 0.5em #faa;
}
#scores .img-select-menu {
	position: relative;
	max-width: 100vw;
	max-height: 10vh;
	overflow: hidden;
}

#scores .img-select {
	max-height: 10vh;
	max-width: 12vw;
	margin: 0.5vw;
	opacity: 0.8;
	cursor: pointer;
}

#scores .img-select:hover {
	transform: scale(1.1);
	opacity: 1;
}

#scores .score-list-background {
	position: absolute;
	max-width: 90vw;
	max-height: 80vh;
	opacity: 0.2;
	z-index: -1;
}

#scores .score-list {
	padding: 2vw 2vw;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
}

#scores .score-listing {
	margin: 0.5em;
	background-color: #fff9;
	padding-left: 0.3em;
}

#scores .score-listing .name {
	margin-right: 1em;
	font-size: 1.3em;
	color: var(--purple);
}

#scores .score-listing .score {
	font-size: 1.1em;
	color: var(--pink);
}

#game button.back {
	z-index: 1;
}

#score-submission {
	height: 100%;
	width: 100%;
	background-color: #fff8;
	position: absolute;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

#score-submission .submit {
	border: none;
	background-color: #5c5e9900;
	color: var(--pink);
	padding: 0.2em 0.5em;
}
#score-submission .submit:hover {
	transform: scale(1.1);
}

#score-submission .form-container {
	padding: 1em;
	padding-top: 0.3em;
	background-color: #5c5e99ee;
	border-radius: 0.5em;
}

#score-submission .score-report {
	font-size: 1.3em;
	margin: 0.3em;
}

#score-submission .score-report .printed-score {
	color: var(--pink);
}

#score-submission .name-error {
	font-size: 0.7em;
	color: red;
}

#score-submission .name-input {
	background-color: #5c5e9900;
	border: none;
	border-bottom: solid 1px black;
}

#score-submission .name-input::-webkit-input-placeholder {
	color: white;
}

#score-submission .name-input:-ms-input-placeholder {
	color: white;
}

#score-submission .name-input::-ms-input-placeholder {
	color: white;
}

#score-submission .name-input::placeholder {
	color: white;
}

#score-submission .name-input:focus {
	outline: none;
}
